.wordcamp-organizer {

	display: grid;
	grid-template-columns: 1fr 3fr;
	grid-row-gap: 30px;
	grid-column-gap: 60px;

	max-width: 800px;
	margin: 0 auto 60px;

	h3 {
		grid-column: 1 / 3;
		border-bottom: solid 1px #126bad;
		line-height: 1em;
	}

	.wordcamp-image__avatar-container img {
		border-radius: 50%;
		border: solid 5px #126bad;
	}

	.wp-block-social-links{
		gap: .5rem;
		margin-top: 2rem;
	}

}
